<template><v-container style="width:400px;height:400px;">
   
<v-card v-show="winnername" height="150px" width="400px" overflow='true'>
        <v-card-title class="headline red lighten-2">
Game Over        </v-card-title>
          <v-spacer></v-spacer>

        <v-card-text >
         
         <h1 x-large v-if="winnername=='draw'" > Match Draw
         </h1>
         <h1  x-large v-else> {{winnername }}  is the winner
         </h1>
         
        </v-card-text>
          <v-spacer></v-spacer>


        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="clear(thils)"
          >
            Restart
          </v-btn>
          <v-spacer></v-spacer>

        </v-card-actions>
      </v-card>
   
  <span v-for="index in 9" :key="index">
 <v-btn raised width="30%" height="30%" x-large :disabled="winnername!=''||cells[index]!=''" :id="index" @click="mark(index)" :value="cells[index]">
     {{cells[index]}}
</v-btn>
  </span>




  clicked {{click}} times and values are
{{cells}}          </v-container>
</template>

<script>


   export default {
       name:'Ticktac',
      
   data() {
         return {
            click:0,
           cells: {
            1: '', 2: '', 3: '',
            4: '', 5: '', 6: '',
            7: '', 8: '', 9: ''
        },
         winConditions: [
            [1, 2, 3], [4, 5, 6], [7, 8, 9], // rows
            [1, 4, 7], [2, 5, 8],    [3, 6, 9], // columns
            [1, 5, 9], [3, 5, 7]             // diagonals
        ],
        winnername:''
            };
      },
   methods: {
      clear(){
       //  alert('clear entered');
         this.click=1;
         while(this.click<'10')
         {
   this.cells[this.click]='';                
            this.click+=1;
         }
  this.click=0;
  this.winnername='';
            }

      
         
         ,
      winner(item){
      item.forEach(element => {
         
         if(this.cells[element[0]]==this.cells[element[1]] && this.cells[element[0]]==this.cells[element[2]] 
         && this.cells[element[0]]!=''){
 //alert('1st value is'+this.cells[element[0]]+ 'second'+this.cells[element[1]]+'third'+this.cells[element[2]])
 this.winnername=this.cells[element[0]];
 //alert("Congratulations "+this.cells[element[0]]+" is the winner");

}

      });
    
     
   }
  ,

            mark(id1) {
         this.click+=1;    
//alert('value is'+id1 + 'and clicked '+this.click);
if(this.click%2==0)
         {
            this.cells[id1]='O';

         }
         else{
            this.cells[id1]='X';
      }

      if(this.click>4)
{
   this.winner(this.winConditions);
   
 
}
if(this.click==9 && this.winnername=='')
{
   this.winnername='draw';
}
   }
   }

   }

</script>

<style scoped>
.row{
  display: flex;
  flex-direction: row;
  gap:.2em;

}

.ticktac-box{
display: block;
box-sizing:border-box;
justify-content: stretch;
  width:5em;
  min-width: 5em;
  min-height: 5em;
  
padding: 1em;
  
}


</style>